body {
  --active-link-color: #00acdc;
  --link-line-height: 60px;
  --active-link-underline-height: 4px;
  background-color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: var(--link-line-height);
  line-height: var(--link-line-height);
  text-decoration: none;
  display: inline-block;
}

.nav__link.is-active, .nav__link:hover {
  color: var(--active-link-color);
}

.nav__link.is-active:after {
  content: "";
  height: var(--active-link-underline-height);
  background-color: var(--active-link-color);
  border-radius: var(--active-link-underline-height);
  bottom: var(--active-link-underline-height);
  display: block;
  position: relative;
}
